@use '../../modules/animation' as animation;
@use '../../shared-components/theme-engine/theme' as theme;

.channel {
  &-list {
    width: 100%;
    height: 100%;
    padding: 10px 0;
    box-sizing: border-box;
  }

  &-view {
    background-color: theme.$background-color-transparent;
    width: 100%;
    height: 100%;
    padding: 100px 0 20px 20px;
    box-sizing: border-box;

    &__name {
      h1 {
        font-size: 18px;
        font-weight: 900;
        margin-top: 8px;
        margin-bottom: 1px;
      }
    }
  }

  &-list__channel {
    display: block;
    padding: 10px 20px;

    &:before {
      content: '# ';
    }

    &:hover {
      cursor: pointer;
      background: #303033;
    }

    &--active {
      color: #fff;
      background: #141415;
      border-left: 3px solid #fb216e;
    }
  }
}

.messages {
  transition: opacity 0.9s ease-out;
  opacity: 1;

  .message__header {
    text-align: center;
    position: relative;
    overflow: hidden;
    margin: 20px 0 25px;
    color: #cccccc;
    &-date {
      border-radius: 0;
      background-color: theme.$background-color-secondary;
      color: theme.$actionable-active-color;
      transition: background-color 0.3s ease-out;
      display: inline-block;
      padding: 3px 20px;
    }
    &:before,
    &:after {
      content: '\a0';
      position: absolute;
      top: 51%;
      overflow: hidden;
      width: 50%;
      height: 1px;
    }
    &:before {
      margin-left: -50%;
      text-align: right;
      background: linear-gradient(90deg, transparent 20%, #c7c7c7 95%, transparent 0);
    }
    &:after {
      content: '\a0';
      position: absolute;
      top: 51%;
      overflow: hidden;
      width: 50%;
      height: 1px;
      background: linear-gradient(270deg, transparent 20%, #c7c7c7 95%, transparent 0);
    }
  }

  .message {
    &__block {
      display: flex;
      justify-content: space-between;
      margin-bottom: 7px;

      &:hover {
        background-color: hsla(0, 0%, 100%, 0.03);

        .message__time {
          visibility: visible;
        }
      }

      &-icon {
        width: 32px;
        height: 32px;
        margin-top: 2px;
        margin-right: 18px;
      }

      &-body {
        background: #3e4042;
        padding: 5px 15px;
        color: #fff;
        white-space: pre-wrap;
        word-break: break-word;
        width: fit-content;
        display: inline-block;

        .message--owner & {
          border-left: 3px solid #fb216e;
          background-color: #212121;
        }
      }

      &-image {
        background-color: #212121;
        transition: background-color 0.3s ease-out;
        position: relative;
        display: inline-block;
        margin: 15px;
        &:before {
          height: 100%;
          width: 3px;
          background-color: #ff006e;
          content: '';
          display: block;
          position: absolute;
          top: 0;
          left: 0;
        }
        img {
          margin-left: 3px;
          width: 330px;
        }
      }

      &-video {
        background-color: #212121;
        transition: background-color 0.3s ease-out;
        position: relative;
        display: inline-block;
        margin: 15px;
        &:before {
          height: 100%;
          width: 3px;
          background-color: #c9fd51;
          content: '';
          display: block;
          position: absolute;
          top: 0;
          left: 0;
        }
        video {
          margin-left: 3px;
          min-height: 186px;
          width: 330px;
        }
      }

      &-audio {
        background-color: #212121;
        transition: background-color 0.3s ease-out;
        position: relative;
        display: inline-block;
        margin: 15px;
        &:before {
          height: 100%;
          width: 3px;
          background-color: #c9fd51;
          content: '';
          display: block;
          position: absolute;
          top: 0;
          left: 0;
        }
        audio {
          margin-left: 3px;
          padding: 10px 15px 10px 20px;
          width: 330px;
        }
      }
    }

    &__time {
      font-size: 12px;
      color: #737373;
      visibility: hidden;
    }

    &.messages__message--first-in-group {
      .message__time {
        visibility: visible;
      }
    }

    .emoji-mart-emoji {
      height: 19px;
    }
  }
}
