.Container {
	.Header {
		display: flex;
		gap: 1rem;
		margin-top: 1rem;

		> * {
			flex: 1;
		}

		.Dao {
			display: flex;
			align-items: center;
			gap: 0.5rem;

			img {
				width: 2.5rem;
				height: 2.5rem;
				border-radius: 0.5rem;
			}

			h1 {
				font-size: 1.5rem;
			}
		}
	}

	.Content {
		margin-top: 2rem;
	}
}
